import { Component, Input, ViewChild } from '@angular/core';
import { NavController, NavParams, ViewController, Slides, Platform, IonicPage } from 'ionic-angular';

/**
 * Generated class for the ModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({ segment: 'page-modal' })
@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html',
})
export class ModalPage {
  @ViewChild(Slides) slides: Slides;
  @Input() data: {};

  constructor(public navCtrl: NavController, public navParams: NavParams, platform: Platform, public viewCtrl: ViewController) {
    this.data = this.navParams.get('data');
    platform.registerBackButtonAction(() => { viewCtrl.dismiss(); });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalPage');
  }

  nextSlide() {
    this.slides.slideNext();
  }

  prevSlide() {
    this.slides.slidePrev();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  getImageSize(item) {
    if (item.imgHeight) {
      return item.imgHeight;
    } else {
      return "auto";
    }
  }

  // TODO - add modal for screenshot Image

}



